
#TopBar {
  z-index: 100;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
}
#TopBar .close {
    margin-top: -51px;
}
#TopBar .close .nav-bar:hover .toggle-bar:before {
      background: #14b97d;
}
#TopBar .close .nav-bar:hover .toggle-bar:after {
      background: #14b97d;
}
#TopBar .close .nav-bar:hover .toggle-bar {
      background: #14b97d;
}
#TopBar .close .nav-bar .toggle-bar {
      height: 5px;
      width: 24px;
      background: #fff;
      position: absolute;
      border-radius: 1px;
      display: block;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      transition: all 0.5s ease-in;
      top: 16px;
}
#TopBar .close .nav-bar .toggle-bar:before {
        height: 5px;
        width: 24px;
        background: #fff;
        position: absolute;
        border-radius: 1px;
        display: block;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        transition: all 0.5s ease-in;
        content: "";
        top: -9px;
}
#TopBar .close .nav-bar .toggle-bar:after {
        height: 5px;
        width: 24px;
        background: #fff;
        position: absolute;
        border-radius: 1px;
        display: block;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        transition: all 0.5s ease-in;
        content: "";
        top: 9px;
}
#TopBar > div {
    transition: all 0.5s ease-in-out;
}
#TopBar nav {
    background: #000;
    border-bottom: 1px solid #555;
}
#TopBar nav .holder {
      padding: 15px 0;
}
#TopBar nav .holder a {
        color: #7b7b7b;
        padding: 0 20px 15px;
        font-size: 16px;
        font-weight: 700;
        transition: all 0.5s ease-in-out;
}
#TopBar nav .holder a:hover {
          color: #14b97d;
}
#TopBar .nav-bar {
    background: #000;
    padding: 12px;
    width: 50px;
    height: 40px;
    margin: 0 auto;
    border: 1px solid #555;
    border-top-color: #000;
    margin-top: -1px;
    border-radius: 0 0 3px 3px;
    position: relative;
    text-align: center;
    cursor: pointer;
}
#TopBar .nav-bar .toggle-bar {
      height: 0;
      width: 24px;
      position: absolute;
      top: 0;
}
#TopBar .nav-bar .toggle-bar:before {
        content: "";
        display: block;
        height: 5px;
        width: 24px;
        top: 16px;
        position: absolute;
        background: #14b97d;
        border-radius: 1px;
        transition: all 1s ease;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
#TopBar .nav-bar .toggle-bar:after {
        content: "";
        display: block;
        height: 5px;
        width: 24px;
        top: 16px;
        position: absolute;
        background: #14b97d;
        border-radius: 1px;
        transition: all 1s ease;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
}



