
#Work {
  background: #000;
  padding-bottom: 20vh;
}
#Work:before {
    content: "";
    display: block;
    height: 12vh;
    background: #000;
    -webkit-transform: skewY(-3deg);
            transform: skewY(-3deg);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}
#Work h2 {
    font-size: 50px;
    color: white;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 20px;
}
#Work h2:after {
      content: "";
      display: block;
      height: 3px;
      width: 1.5em;
      background: #14b97d;
      margin: 0 auto;
}
#Work .holder {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#Work .holder .pro {
      padding: 50px 0;
}
#Work .holder .pro:first-child a {
        background-image: url(../assets/img/resumer.jpg);
}
#Work .holder .pro:nth-child(2) a {
        background-image: url(../assets/img/tips.jpg);
}
#Work .holder .pro:nth-child(3) a {
        background-image: url(../assets/img/music.jpg);
}
#Work .holder .pro a {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        display: block;
        width: 30vw;
        height: 35vh;
        margin-right: 10px;
        position: relative;
}
#Work .holder .pro a:hover .discribe {
          opacity: 1;
}
#Work .holder .pro a .discribe {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-pack: center;
              -ms-flex-pack: center;
                  justify-content: center;
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
              -ms-flex-direction: column;
                  flex-direction: column;
          background: #14b97d;
          color: white;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          opacity: 0;
          transition: all .8s;
}
#Work .holder .pro a .discribe h3 {
            font-size: 20px;
            font-weight: 700;
}



