
#ProDiscribe:before {
  content: "";
  display: block;
  height: 12vh;
  background: #fff;
  -webkit-transform: skewY(-3deg);
          transform: skewY(-3deg);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}
#ProDiscribe .holder {
  height: 70vh;
}
#ProDiscribe .holder .discribe {
    width: 60vw;
    height: 45vh;
    margin: 0 auto;
}
#ProDiscribe .holder .discribe h2 {
      font-size: 50px;
      color: black;
      font-weight: 700;
      text-transform: uppercase;
      margin-bottom: 20px;
}
#ProDiscribe .holder .discribe h2:after {
        content: "";
        display: block;
        height: 3px;
        width: 1.5em;
        background: black;
        margin: 0 auto;
}
#ProDiscribe .holder .discribe p {
      padding-top: 10px;
      font-size: 22px;
      line-height: 1.5em;
}
#ProDiscribe .holder .tab {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#ProDiscribe .holder .tab li {
      list-style: none;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background: #d5e0dc;
      cursor: pointer;
}
#ProDiscribe .holder .tab li:nth-child(2) {
      margin: 0 20px;
}
#ProDiscribe .holder .tab .active {
      background: #14b97d;
}



