
.fade-enter-active, .fade-leave-active {
  transition: opacity 1.5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
#About:before {
  content: "";
  display: block;
  height: 12vh;
  background: #fff;
  -webkit-transform: skewY(-3deg);
          transform: skewY(-3deg);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}
#About h2 {
  font-size: 50px;
  font-weight: 700;
  text-transform: uppercase;
}
#About h2:after {
    content: "";
    display: block;
    height: 3px;
    width: 1.5em;
    background: #000;
    margin: 0 auto;
}
#About .tabs .tab {
  margin-top: 20px;
}
#About .tabs .tab .active {
    color: #14b97d;
}
#About .tabs .tab .active:after {
      background: #14b97d;
      content: "";
      display: block;
      height: 2px;
      width: 100%;
      margin: 0 auto;
      transition: all .5s;
      margin-top: 2px;
}
#About .tabs .tab a {
    font-size: 30px;
    font-weight: 600;
    color: inherit;
    padding: 30px;
    display: inline-block;
}
#About .tabs .tab a:after {
      content: "";
      display: block;
      height: 2px;
      /*width:1.5em;*/
      width: 0;
      background: #000;
      margin: 0 auto;
      transition: all .5s;
      margin-top: 2px;
}
#About .tabs .tab a:hover:after {
      width: 100%;
}
#About .tabs .tab-content {
  width: 60%;
  height: 100vh;
  margin: 0 auto;
  position: relative;
}
#About .tabs .tab-content .profile {
    position: absolute;
    top: 0;
}
#About .tabs .tab-content .profile p {
      text-align: justify;
      padding-bottom: 16px;
      font-size: 20px;
}
#About .tabs .tab-content .profile .btns {
      margin-top: 50px;
}
#About .tabs .tab-content .profile .btns a {
        margin-right: 20px;
        color: #000;
        transition: color .5s;
        text-transform: uppercase;
}
#About .tabs .tab-content .profile .btns a:hover {
          color: white;
}
#About .tabs .tab-content .skills {
    margin-top: 20px;
    width: 100%;
    position: absolute;
    top: 0;
}
#About .tabs .tab-content .skills > div {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      background: #14b97d;
      height: 25px;
      border-radius: 3px;
      margin-bottom: 20px;
      line-height: 25px;
      text-align: justify;
      padding-left: 10px;
      color: white;
      font-weight: 700;
}
#About .tabs .tab-content .skills > div:after {
        position: relative;
        right: -3em;
        color: black;
}
#About .tabs .tab-content .skills .html {
      width: 90%;
}
#About .tabs .tab-content .skills .html:after {
        content: "90%";
}
#About .tabs .tab-content .skills .css {
      width: 80%;
}
#About .tabs .tab-content .skills .css:after {
        content: "80%";
}
#About .tabs .tab-content .skills .javascript {
      width: 85%;
}
#About .tabs .tab-content .skills .javascript:after {
        content: "85%";
}
#About .tabs .tab-content .skills .jquery {
      width: 80%;
}
#About .tabs .tab-content .skills .jquery:after {
        content: "80%";
}
#About .tabs .tab-content .skills .vue {
      width: 70%;
}
#About .tabs .tab-content .skills .vue:after {
        content: "70%";
}
#About .tabs .tab-content .skills .webpack {
      width: 60%;
}
#About .tabs .tab-content .skills .webpack:after {
        content: "60%";
}
#About .tabs .tab-content .skills .photoshop {
      width: 50%;
}
#About .tabs .tab-content .skills .photoshop:after {
        content: "50%";
}



