
#Contact {
  background: #14b97d;
  color: white;
}
#Contact:before {
    content: "";
    display: block;
    height: 12vh;
    background: #14b97d;
    -webkit-transform: skewY(-3deg);
            transform: skewY(-3deg);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}
#Contact h2 {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
}
#Contact .methods {
    padding: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#Contact .methods .wrapper {
      display: inline-block;
}
#Contact .methods .wrapper i:first-child {
        margin-right: 40px;
}
#Contact .methods i {
      font-size: 22px;
}
#Contact .methods i a {
        color: inherit;
}
#Contact .methods i a:hover {
          text-decoration: underline;
}
#Contact .methods i:before {
        font-size: 30px;
        vertical-align: top;
        margin-right: 10px;
}
#Contact .logos {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#Contact .logos .wrapper {
      width: 30vw;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
#Contact .logos a {
      display: inline-block;
      width: 60px;
      height: 60px;
      border: 3px solid white;
      border-radius: 50%;
      line-height: 60px;
      color: inherit;
      transition: all .5s ease-in-out;
}
#Contact .logos a:before {
        font-size: 30px;
}
#Contact .logos a:hover {
        background: white;
}
#Contact .logos a:hover:before {
          color: #14b97d;
}
#Contact .btn {
    padding-top: 30px;
    padding-bottom: 20px;
}
#Contact .btn a {
      border: 1px solid white;
      transition: color .5s ease-out;
      text-transform: uppercase;
}
#Contact .btn a:before {
        background: white;
}
#Contact .btn a:hover {
        color: #14b97d;
}
#Contact .copyright {
    padding-bottom: 100px;
}
#Contact .weixin {
    position: fixed;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.2);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}



